$color-primary: #a7c0e8;
$color-primary-light: #e6f1ff;
$color-primary-dark: #6f88b1;
$color-grey-1: #d9dbe2;
$color-grey-2: #70737c;
$color-grey-3: #30333c;
$color-red: #e36464;
$color-white: #fff;
$color-black: #000;

$color-gradient-1: #e8f1ff;
$color-gradient-2: #fafcff;

$box-shadow: 0px 16px 36px -4px rgba(167, 192, 232, 0.64);
$table-gradient: linear-gradient(
  to right,
  rgba($color-gradient-1, 0.7),
  rgba($color-gradient-2, 1)
);

$width-xl: 136rem;
$width-l: 120rem;

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
}

body {
  box-sizing: border-box;
  font-family: Montserrat-Medium;
}

p {
  font-family: Montserrat-Medium;
  font-size: 1.8rem;
  color: $color-grey-2;
}
